import { Component } from "react";
import NavBar from "./NavBar.jsx";
import WeekDayBar from "./WeekDayBar.jsx";
import TimeTable from "./TimeTable.jsx";
import ls from 'local-storage'

class App extends Component {
	constructor(props) {
		super(props);

		this.state = {};

		this.switchTheme = this.switchTheme.bind(this);
		this.changeDay = this.changeDay.bind(this);
	}

	componentDidMount() {
		const date = new Date();
		const day = date.getDay();
		this.setState({
			colorTheme: ls.get("colorTheme") || "light-theme",
			currentDay: day < 1 || day > 5 ? 0 : day - 1,
			today: day < 1 || day > 5 ? false : day - 1,
		}, () => {
			this.switchTheme(this.state.colorTheme);
		})
	}

	switchTheme(newTheme) {
		this.setState(
			(state) => {
				document.body.classList.remove(state.colorTheme);
				document.body.classList.add(newTheme);

				ls.set("colorTheme", newTheme);

				return {
					colorTheme: newTheme
				};
			}
		);
	}

	changeDay(newDay) {
		this.setState({
			currentDay: newDay,
		});
	}

	render() {
		return (
			<div className="App">
				<WeekDayBar
					onDayChange={this.changeDay}
					currentDay={this.state.currentDay}
				/>
				<TimeTable
					day={this.state.currentDay}
					today={this.state.today}
				/>
				<NavBar onThemeSwitch={this.switchTheme} />
			</div>
		);
	}
}

export default App;
